import messageList from "@/components/mycom/messageList.vue"
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
import empty from "@/components/mycom/empty.vue"
export default {
	components: {
		messageList,
		uniLoadMore,
		empty
	},
	data() {
		return {
			page: 1,
			totalPage: 1,
			status: 'loading',
			dataList: [],
			show: false
		}
	},
	onLoad() {
		// this.getShare();
		this.$nextTick(function() {
			this.loadData()
		})
	},
	onReachBottom() {
		this.status = 'loading';
		this.totalPage > this.page ?
			((this.page += 1), this.loadData()) :
			setTimeout(() => {
				this.status = 'noMore';
			}, 30);
	},
	methods: {
		loadData() {
			let parmas = {
				uid: getApp().globalData.uid,
				pageNo: this.page,
				pageCount: "10"
			};
			console.log(parmas);
			this._reqw
				.ipost(parmas, 'messageList')
				.then(res => {
					console.log(res)
					res.result == 0 ?
						((res.totalPage > 1 ? this.status = 'more' : this.status = 'noMore'), this.totalPage = res.totalPage,
							(res.dataList.length == 0 ? this.show = true : res.dataList.forEach(item => {
								this.dataList.push(item)
							}))
						) :
						this.$api.tip(res.resultNote);
				})
				.catch(err => {});
		}
	}
}
